import React from "react";
import TableView from './TableView'
import ModalView from "./ModalView";
import AddModalContent from "./AddModalContent";
import EditModalContent from "./EditModalContent";
import TeamMemberHeader from "./TeamMemberHeader";

class TeamMemberView extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            isAddModalOpen:false,
            isEditModalOpen:false,
            data:{}
        }
       
        
        

        this.source = this.props.source;

       
    }

    componentDidMount() {
        this.props.fetchTeamMemberList()
    }

   

    render() {
        const head1Data=["#","TEAM MEMBER NAME","JOINING DATE","LOGIN ID","EMPLOYEE TYPE","ROLE","ACTIONS"]

        // function editModal(obj){
        //     return(
        //     <ModalView submitAction={this.props.editTeamMemberData} isModalOpen={true} />
        //     )
        // }
        const addModal=<AddModalContent  closeModal={()=>this.setState({isAddModalOpen:false})} submitAction={this.props.addNewTeamMemberData} />;
        const editModal=<EditModalContent  data={this.state.data} closeModal={()=>this.setState({isEditModalOpen:false})} submitAction={this.props.editTeamMemberData}/>;
        return (
            <div className="team-member-container">
            <TeamMemberHeader openModal={()=>this.setState({isAddModalOpen:true})}/>
            <TableView headData={head1Data} bodyData={this.props.data} setData={(data)=>this.setState({data:data})} delAction={this.props.deleteTeamMemberData} className={'member-table'} openModal={()=>this.setState({isEditModalOpen:true})} closeModal={()=>this.setState({isAddModalOpen:false})} />
            
            <ModalView  title={'ADD TEAM MEMBER'} isModalOpen={this.state.isAddModalOpen} closeModal={()=>this.setState({isAddModalOpen:false})} type={addModal}/>
            <ModalView title={'EDIT TEAM MEMBER DATA'} isModalOpen={this.state.isEditModalOpen} openModal={()=>this.setState({isEditModalOpen:true})} closeModal={()=>this.setState({isEditModalOpen:false})}  type={editModal} />
    </div>
        )
    }

    

    componentWillUnmount() {
        document.removeEventListener("keydown", this._handleKeyDown);
        document.removeEventListener("click", this._handleClickEvents);
        window.removeEventListener("resize", this._handleWindowResize);
    }
}

export default TeamMemberView;
