import React from "react";

class ModalView extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
    };
  }

  render() {

    if(!this.props.isModalOpen)
    return null;

    return (
      <div className="modal">
        <div className="modal-content">
          <div className="modal-header">
            <h4 className="modal-title">{this.props.title}</h4>
          </div>
          <div className="modal-body">
          
          {/* {this.props.type} */}
          {/* <AddModalContent  closeModal={this.props.closeModal} submitAction={this.props.submitAction}/> */}
          {this.props.type}
          </div>
          <div className="modal-footer">
            <button onClick={this.props.closeModal}className="close-button">Close</button>
            <button form="modal-form" type="submit" className="submit-button">Submit</button>

          </div>
        </div>
      </div>
    );
  }
}

export default ModalView;
