import React from 'react'

class TableView extends React.Component{

  

    render(){

        return(
            <table className={this.props.className}>
                <thead >
                    <tr>
                    {this.props.headData.map((data,index)=>{
                        return (
                            <th key={data}>{data}</th>
                        )
    
                    })}
    
                    </tr>
    {/* <tr><th>ID</th></tr> */}
                </thead>
                <tbody>
                    {
                        this.props.bodyData.map((row,index)=>{
                            return(
                            <tr key={index}>{
                                
                                <>
                                <td>{index+1}</td>
                                 <td>{row["team_member_name"]}</td>
                                 <td>{row["joining_date"]}</td>
                                 <td>{row["login_id"]}</td>
                                 <td>{row["employee_type"]}</td>
                                 <td>{row["role"]}</td>
                                 
                                <td>
                                <button  onClick={()=>{this.props.setData(row);this.props.openModal()}} className="edit-button">Edit</button>
                                <button  onClick={()=>{this.props.delAction(row["id"])}} className="delete-button">X</button> 
                                </td>
                                
                                
                                </>

                                } 
                            </tr>
                            // <tr></tr>
                            
                            )
    
                       
                        })
                    }
    
                </tbody>
            </table>
        )
    }
}

export default TableView;