import React from "react";

class AddModalContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = 
    {
    name: "",
    joiningDate:"",
    loginId:"",
    employeeType:"",
    role:""  
  };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    // const value=event.target.value;
    this.setState({[event.target.name]: event.target.value});
    // console.log(this.state)
  }

  handleSubmit(event) {
    alert('Your Data is Added for name : ' + this.state.name);
    const data={
      "team_member_name":this.state.name,
      "login_id":this.state.loginId,
      "employee_type":this.state.employeeType,
      "role":this.state.role
    }
    console.log(data)
    this.props.submitAction(data)
    this.props.closeModal();
    // AppUtil.ajax(TEAM_MEMBER_CONSTANT.URL,data)
    event.preventDefault();
  }

  render() {
    return (
      <form id="modal-form" onSubmit={this.handleSubmit}>
        {/* <div className="form-input-container"> */}
        <label className="form-label"> 
          Full Name:
          <input  className="form-input" type="text" name="name" value={this.state.name} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Login Id :
          <input className="form-input" type="text" name="loginId"value={this.state.loginId} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Employee Type :
          <input className="form-input" type="text" name="employeeType" value={this.state.employeeType} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Role :
          <input className="form-input" type="text" name="role" value={this.state.role} onChange={this.handleChange} />
        </label>
        {/* </div> */}
        
        
        {/* <input className="form-label" type="submit" value="Submit" /> */}
      </form>
    );
  }
}

export default AddModalContent;
