
import { TEAM_MEMBER_CONSTANT } from '../constants/TeamMemberConstants'


const initialState={
    data:[],
    isShown:false
}

const TeamMemberReducer=(state=initialState,action)=>{

    switch(action.type){
     case TEAM_MEMBER_CONSTANT.GET_TEAM_MEMBER_DATA:
        console.log("Inside case")
        return{
            ...state,
        isShown:true,
    data:action.payload
        }
    case TEAM_MEMBER_CONSTANT.ADD_TEAM_MEMBER_DATA:
        console.log("payload",action.payload)
        return{
            ...state,
            data:[...state.data,action.payload]
        }

    case TEAM_MEMBER_CONSTANT.DELETE_TEAM_MEMBER_DATA:
        console.log(action.payload)
        function matchId(obj){
            return obj["id"]!==action.payload
        }
        const currData=state.data;
        return{
            ...state,
            data:currData.filter(matchId)
        }

        case TEAM_MEMBER_CONSTANT.EDIT_TEAM_MEMBER_DATA:
            console.log(action.payload)
            function findIndex(data,id){

                for(let i=0;i<data.length;i++){
                    if(data[i]["id"]===id){
                        console.log("Index",i)
                        return i;
                    }
                }

            }
            function updateData(data,obj){
                const index=findIndex(data,obj["id"]);
                const newObj={...data[index],...obj}
                console.log("Index2",index)
                data.splice(index,1,newObj)
                console.log("data1",data)
                return data;
            }
            return{
                ...state,
                data:[...updateData(state.data,action.payload)]
            }
    default :
      return state;

    }

    
}


export default TeamMemberReducer;