import React from "react";

class TeamMemberHeader extends React.Component {
    constructor(props) {
        super(props);
        this.source = this.props.source;

        this.state={
            isModalOpen:false
        }
    }

    componentDidMount() {
        // this.props.fetchTeamMemberList()
    }

    render() {
        

        return (
            <nav>
                <span className="pageHeading">TEAM  MEMBER</span>
                <button className="add-button" onClick={this.props.openModal}>Add +</button>

            </nav>
  
        )
    }

    

    componentWillUnmount() {
        document.removeEventListener("keydown", this._handleKeyDown);
        document.removeEventListener("click", this._handleClickEvents);
        window.removeEventListener("resize", this._handleWindowResize);
    }
}

export default TeamMemberHeader;