import React from "react";

class EditModalContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {...props.data}
    console.log(props.data)
//     {
//     name: "",
//     joiningDate:"",
//     loginId:"",
//     employeeType:"",
//     role:""  
//   };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    // const value=event.target.value;
    this.setState({[event.target.name]: event.target.value});
    // console.log(this.state)
  }

  handleSubmit(event) {
    alert('Edited Details for id',this.state.id);
    const data={
      "team_member_name":this.state.team_member_name,
      "login_id":this.state.login_id,
      "employee_type":this.state.employee_type,
      "role":this.state.role,
      "id":this.state.id
    }
    console.log(data)
    this.props.submitAction(data)
    this.props.closeModal();
    // AppUtil.ajax(TEAM_MEMBER_CONSTANT.URL,data)
    event.preventDefault();
  }

  render() {
    return (
      <form id="modal-form" onSubmit={this.handleSubmit}>
        {/* <div className="form-input-container"> */}
        <label className="form-label"> 
          Full Name:
          <input  className="form-input" type="text" name="team_member_name" value={this.state.team_member_name} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Login Id :
          <input className="form-input" type="text" name="login_id"value={this.state.login_id} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Employee Type :
          <input className="form-input" type="text" name="employee_type" value={this.state.employee_type} onChange={this.handleChange} />
        </label>
        <label className="form-label">
          Role :
          <input className="form-input" type="text" name="role" value={this.state.role} onChange={this.handleChange} />
        </label>
        {/* </div> */}
        
        
        {/* <input className="form-label" type="submit" value="Submit" /> */}
      </form>
    );
  }
}

export default EditModalContent;
