import {connect} from 'react-redux';
import { addNewTeamMemberData, deleteTeamMemberData, editTeamMemberData, fetchTeamMemberList } from '../actions/TeamMemberActions';
import TeamMemberView from './TeamMemberView'
const mapStateToProps = (state,ownProps) => {
    const { data } = state.teamMemberData

    return {
       data,
        store: ownProps.state
    }
}
// const mapStateToProps=state=>{
//     console.log(state.teamMemberData)
//     return {
//         data:state.teamMemberData.data
//     }
// }

const mapDispatchToProps=dispatch=>{

    return{
        fetchTeamMemberList:()=>{
            dispatch(fetchTeamMemberList())
        },
        addNewTeamMemberData:(formData)=>{
            dispatch(addNewTeamMemberData(formData))
        },
        deleteTeamMemberData:(id)=>{
            dispatch(deleteTeamMemberData(id))
        },
        editTeamMemberData:(obj)=>{
            dispatch(editTeamMemberData(obj))
        }
    }
}


export default connect(
    
    mapStateToProps,mapDispatchToProps
)(TeamMemberView)