
import {APP_URL_CONSTANTS} from 'js/common/constants/GlobalConstants'
import AppUtil from '../../../../js/utils/AppUtil';
import { TEAM_MEMBER_CONSTANT } from '../constants/TeamMemberConstants';


export const fetchTeamMemberList=() =>{

    return dispatch=>{
       
        
        AppUtil.ajax(TEAM_MEMBER_CONSTANT.URL,null,reponse=>{
            console.log(reponse);
            dispatch(populateTeamMemberList(reponse.team_member_data))
        })
    }

}


export const populateTeamMemberList= data =>{
console.log(data)
    return {
        type:TEAM_MEMBER_CONSTANT.GET_TEAM_MEMBER_DATA,
        payload:data
    }
}

export const addNewTeamMemberData=(formData)=>{

console.log("Form Data",formData)

    return dispatch=>{
        AppUtil.ajax("http://localhost:8080/Member_list_API/member_controller",null,response=>{
            console.log("Sucessful Fetch")
          
            dispatch(getAddMemberFormData(response,formData))
        },undefined,false,{body:JSON.stringify(formData),method:"POST"})
    }
}

export const getAddMemberFormData=(response,formData)=>{
        console.log("data",formData)
        const date=response.joining_date;
        const id=response.id;
    return{
        type:TEAM_MEMBER_CONSTANT.ADD_TEAM_MEMBER_DATA,
        payload:{...formData,joining_date:date,id:id}
    }

}


export const deleteTeamMemberData=id=>{
console.log(typeof id)
const jsonObj=JSON.stringify({"id":id});

    return dispatch=>{

        AppUtil.ajax(
            
        "http://localhost:8080/Member_list_API/member_controller",
        null,
        response=>{
            console.log(" Delete Sucessful")
        
            dispatch(onDeleteTeamMemberDataSucess(id))
        },
        fail=>{
            console.log("fail")
            // dispatch(onDeleteTeamMemberDataSucess(id))
        },
        false,
        {body:jsonObj,method:"DELETE"}
        )


    }

}

export const onDeleteTeamMemberDataSucess=id=>{

    return {
        type:TEAM_MEMBER_CONSTANT.DELETE_TEAM_MEMBER_DATA,
        payload:id
    }

}

export const editTeamMemberData=obj=>{


    return dispatch=>{

        AppUtil.ajax("http://localhost:8080/Member_list_API/member_controller",null,response=>{
            console.log(" Edit Sucessful")
            console.log("Object ",obj)
        
            dispatch(onEditTeamMemberDataSucess(obj))
        },fail=>{
            console.log("Edit fail")
        },false,{body:JSON.stringify(obj),method:"PUT"})


    }

    

}
export const onEditTeamMemberDataSucess=obj=>{

    return {
        type:TEAM_MEMBER_CONSTANT.EDIT_TEAM_MEMBER_DATA,
        payload:obj
    }

}