/* istanbul ignore file */

import {
    createStore,
    applyMiddleware,
    compose,
    combineReducers
} from 'redux';

import thunk from 'redux-thunk';
import MessageReducer from 'js/modules/message/reducers/MessageReducer';
import appReducer from 'js/app/reducers/AppReducer';
import TeamMemberReducer from 'js/modules/team_member/reducers/TeamMemberReducer';
const rootReducer = combineReducers({
    messageData: MessageReducer,
    appData: appReducer,
    teamMemberData:TeamMemberReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore( rootReducer, composeEnhancers( applyMiddleware(thunk) ) );

export default store;
